.collection-items {
    position:relative;
    .container-sizing;
    .padding-all-small;
    .loading {
        position:absolute;
        width:100%;
        top:0;
        z-index:10;
        text-align: center;
    }
    .loading-inner {
        padding:0;
    }
}

.collection-item {
    margin-bottom:@space-small;
    background-color: @content-background;
	border: 1px solid @ui-grey-dark;
    border-radius:3px;
    overflow: hidden;
}

.collection-item-content {
	.padding-all-small;
}

.collection-item-content-title {
    .font-style-regular;
    .font-color-dark;
}

.collection-item-content-body {
    .font-style-small-light;
    .font-color-light;
}

.collection-item-actions {
    padding: 0 @space-x-small;
    text-align: right;
    background-color: @ui-grey-lightest;

    button {
        margin-left: @space-micro;
        padding: @space-micro @space-x-small;
        font-size:12px;
    }
}
